html, body {
  margin: 0;
  height: 100%;
  min-height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

header,
footer {
  flex-shrink: 0;
  width: auto;
}

main {
  flex: 1 0 auto;
  width: auto;
  max-width: calc(100vw - (100vw - 100%));
  overflow-x: hidden;
}

body {
  background: #fff;
  font-family: Ubuntu, system-ui;
}

header,
footer {
  padding: 1.5rem 0;
  background: #d0d1cf;
  text-align: center;
}
